export default { 
	data() {
		return {
			nun:1,
			   img:[],
			 scale:1,
			 startData:{
				clientX:'',
				clientY:''
			},
			startTime:'',
		};
	},
	onLoad(option) {
		this.index(option);
	}, 
	methods: {
		index(option){
			var url=JSON.parse(decodeURIComponent(option.url)).toString();
			var notS = url.split(':')[0];
			var a = notS.indexOf('ps') > -1;
			if(a){
				var ig=[];
				ig=url.split(",")
				this.img=ig;
			}else{
				var num=Math.floor(Math.random()*8+1);
				var imgs='../../static/images/icon/img/title'+num+'.png'
				this.img.push(imgs);
			}			
		},
		onScale(){
		                
		            },
		start(e){
			console.log('start')      
		         console.log(e)                       
			this.startData.clientX=e.changedTouches[0].clientX;
						 
			this.startData.clientY=e.changedTouches[0].clientY;
			this.startTime=e.timeStamp;
		},
		end(e){
			console.log('end')  
			console.log(e)      
			const subX=e.changedTouches[0].clientX-this.startData.clientX;
			const subY=e.changedTouches[0].clientY - this.startData.clientY;
			const  time=e.timeStamp-this.startTime;
			
			if(subY>50 || subY<-50){
			//上下
			}else{
				if(subX>100&&time<200){
					// 右滑动
					if(this.nun>1&&this.nun<=this.img.length){
						this.nun--;
					}
				}else if(subX<-100&&time<200){
					// 左滑
					if(this.nun<this.img.length){
						this.nun++;
					}
				}else{
					
				}
			}
			}
	}
};
